                  top: 20px;
                  right: 30px;
                "
              />
              <div
                id="gasChart"
                :style="{
                  width: '600px',
                  height: '250px',
                  marginTop: '-30px',
                }"
              ></div>
              <div class="subText">成本管理</div>
              <div class="classIfyBox">
                <div class="classIfyInfo">
                  <div class="classIfyText">
                    <p><span>总支出</span><span>￥24,150</span></p>
                    <el-progress
                      :percentage="30"
                      :show-text="false"
