                    ></el-progress>
                  </div>
                </div>
                <div class="classIfyInfo">
                  <div class="classIfyText">
                    <p><span>总收入</span><span>￥24,150</span></p>
                    <el-progress
                      :percentage="30"
                      :show-text="false"
