                </div>
                <div class="classIfyInfo">
                  <div class="classIfyText">
                    <p><span>收入</span><span>￥24,150</span></p>
                    <el-progress
                      :percentage="30"
                      :show-text="false"
                      color="#39DA8A"
                    ></el-progress>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </dv-border-box-1>
      </div>
      <div class="bottomWrap">
        <div class="classify">
          <dv-border-box-12>
            <div class="box">
              <dv-loading v-if="loadding">Loading...</dv-loading>
              <div v-else>
                <v-title text="营业额"></v-title>
                <div class="weekBox">
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 10%"></div>
                    </div>
                    <span>S</span>
                  </div>
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 20%"></div>
                    </div>
                    <span>M</span>
                  </div>
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 30%"></div>
                    </div>
                    <span>T</span>
                  </div>
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 40%"></div>
                    </div>
                    <span>W</span>
                  </div>
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 50%"></div>
                    </div>
                    <span>T</span>
                  </div>
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 60%"></div>
                    </div>
                    <span>F</span>
                  </div>
                  <div class="week">
                    <div class="pillar">
                      <div class="pillarColor" style="height: 70%"></div>
                    </div>
                    <span>S</span>
                  </div>
                </div>
                <div class="rank">
                  <img src="../assets/icon_arrows.png" alt="" class="arrows" />
                  <p><span>最畅销</span><span>最畅销</span></p>
                  <div class="line"></div>
                  <span>28.6t</span>
                </div>
                <div class="rank">
                  <img
                    src="../assets/icon_arrows_gray.png"
                    alt=""
                    class="arrows"
                    style="transform: rotate(90deg)"
                  />
                  <p><span>最畅销</span><span>最畅销</span></p>
                  <div class="line"></div>
                  <span>28.6t</span>
                </div>
              </div>
            </div>
          </dv-border-box-12>
        </div>
        <div class="powerChart">
          <dv-border-box-12>
            <div class="box">
              <dv-loading v-if="loadding">Loading...</dv-loading>
              <div v-else>
                <v-title text="公区用量"></v-title>
                <div
                  id="histogramChart"
                  :style="{
                    width: '280px',
                    height: '185px',
                    marginTop: '-35px',
                  }"
                ></div>
              </div>
            </div>
          </dv-border-box-12>
        </div>
      </div>
    </div>
    <div class="rightWrap">
      <div class="storeGas">
        <dv-border-box-9>
          <div class="box">
