            <div v-else>
              <v-title text="商铺用量"></v-title>
              <div class="storeContent">
                <div class="tabs">
                  <div class="active">1F</div>
                  <div>2F</div>
                  <div>3F</div>
                  <div>4F</div>
                </div>
                <div class="list">
                  <dv-scroll-board
                    :config="list"
                    style="width: 420px; height: 170px; margin-top: 20px"
                  />
                </div>
              </div>
              <div class="storeData">
                <div><span>月度用水量</span><span>182t</span></div>
                <div><span>当前水价</span><span>2.3元/t</span></div>
              </div>
              <div class="storeData">
                <div><span>年度用水量</span><span>1872t</span></div>
                <div><span>商铺用水量</span><span>1872t</span></div>
              </div>
            </div>
          </div>
        </dv-border-box-9>
