</template>

<script>
import title from "../components/title.vue";
function formatter(number) {
  const numbers = number.toString().split("").reverse();
  const segs = [];
  while (numbers.length) segs.push(numbers.splice(0, 3).join(""));
  return segs.join(",").split("").reverse().join("");
}
export default {
  data: () => {
    return {
      list: {
        data: [
          //商铺电费列表
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
          ["101 恒昌药店", "用水量15564t", "2月水费:已交"],
        ],
        rowNum: 4,
        oddRowBGC: "#122A56",
        evenRowBGC: "#1D4282",
      },
      config: {
        value: 66,
      },
      nowPower: {
        number: [123456],
        content: "{nt}￥",
        formatter,
      },
      loadding: false,
    };
  },
  components: {
    "v-title": title,
  },
  mounted: function () {
    // 绘制echarts
    this.drawLine();
  },

  methods: {
    drawLine() {
      //燃气费面积图
      let gasChart = this.$echarts.init(document.getElementById("gasChart"));
      gasChart.setOption({
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        grid: {
          left: "0%",
          bottom: "10%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            data: [
              "1月",
              "2月",
              "3月",
              "4月",
              "5月",
              "6月",
              "7月",
              "8月",
              "9月",
              "10月",
              "11月",
              "12月",
            ],
            axisLabel: {
              color: "#fff",
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              color: "#fff",
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#34496F",
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "燃气费",
            type: "line",
            stack: "总量",
            areaStyle: {
              color: "#52b3d9",
            },
            emphasis: {
              focus: "series",
            },
            data: [120, 132, 101, 134, 90, 230, 210, 32, 321, 544, 657, 87],
            smooth: true,
          },
        ],
      });
      //电费柱状图
      let histogramChart = this.$echarts.init(
        document.getElementById("histogramChart")
      );
      histogramChart.setOption({
        grid: {
          left: "0%",
          bottom: "0%",
          containLabel: true,
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "line",
            label: {
              backgroundColor: "#6a7985",
            },
          },
          formatter: "{b}<br />{c}t",
        },
        xAxis: {
          type: "category",
          data: ["1F", "2F", "3F", "4F"],
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(98, 217, 255, .3)",
            },
          },
          axisLabel: {
            color: "#fff",
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            color: "#fff",
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: "rgba(98, 217, 255, .3)",
            },
          },
        },
        series: [
          {
            data: [120, 200, 150, 80],
            type: "bar",
            itemStyle: {
              color: "#3EC1EF",
            },
            label: {
              show: true,
              position: "top",
              color: "#fff",
              formatter: "{c}w",
            },
          },
        ],
      });
      // 燃气消耗仪表盘
      let gasExpend = this.$echarts.init(document.getElementById("gasExpend"));
      gasExpend.setOption({
        title: {
          show: true,
          text: "每月消耗燃气",
          textStyle: {
            color: "#fff",
            fontSize: 16,
          },
          padding: [5, 0, 0, 0],
        },
        series: [
          {
            type: "gauge",

            data: [
              {
                value: 45,
                detail: {
                  color: "#fff",
                  offsetCenter: [0, 0],
                  formatter: function (value) {
                    return value + "%";
                  },
                },
              },
            ],
            startAngle: 0,
            endAngle: 359.999,
            axisTick: {
              distance: -10,
              length: 10,
              lineStyle: {
                color: "#103A5B",
                width: 2,
              },
            },
            axisLabel: {
              show: false,
            },
            pointer: {
              show: false,
            },
            axisLine: {
              lineStyle: {
                width: 10,
                color: [
                  [0.25, "#049DAD"],
                  [1, "#1A292A"],
                ],
              },
            },
            splitLine: {
              show: false,
            },
          },
        ],
      });
      // 燃气成本
      let gasCost = this.$echarts.init(document.getElementById("gasCost"));
      gasCost.setOption({
        title: {
          show: true,
          text: "燃气消耗成本",
          textStyle: {
            color: "#fff",
            fontSize: 16,
          },
        },
        series: [
          {
            type: "gauge",
            center: ["50%", "55%"],
            axisLine: {
              lineStyle: {
                width: 10,
                color: [
                  [0.3, "#0599A9"],
                  [0.7, "#DB8F2C"],
                  [1, "#CF7561"],
                ],
              },
            },
            pointer: {
              icon: "rect",
              width: 2,
              length: "80%",
              itemStyle: {
                color: "#077E95",
              },
            },
            axisTick: {
              distance: -10,
              length: 10,
              lineStyle: {
                color: "#103A5B",
                width: 2,
              },
            },
            splitLine: {
              show: false,
            },
            axisLabel: {
              color: "#fff",
              distance: -40,
            },
            detail: {
              valueAnimation: true,
              formatter: "{value} %",
              color: "#FFFFFF",
              offsetCenter: [0, "60%"],
              fontSize: 22,
            },
            title: {
              offsetCenter: [0, "25%"],
              textStyle: {
                fontSize: 15,
                color: "white",
              },
            },
            data: [
              {
                value: 70,
                name: "月人均费用预算",
              },
            ],
          },
        ],
      });
    },
  },
};
</script>

<style lang="less" scoped>
// 布局
.contentWrap {
  display: flex;
