                background-color: #35b1ff;
                width: 10px;
              }
            }
          }
        }
        .rank {
          display: flex;
          align-items: center;
          margin-top: 10px;
          font-size: 14px;
          .arrows {
            width: 22px;
            height: 22px;
            transform: rotate(-90deg);
            margin-right: 6px;
            flex-shrink: 0;
          }
          p {
            display: flex;
            flex-direction: column;
            align-items: center;
            flex-shrink: 0;
          }
          .line {
            width: 110px;
            height: 4px;
            background: #ffffff;
            border-radius: 2px;
            margin: 0 25px;
            flex-shrink: 0;
          }
          span {
            flex-shrink: 0;
          }
        }
      }
      .powerChart {
        width: 429px;
        height: 295px;
      }
    }
  }
  .rightWrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 20px;
    .gasBoard {
      width: 739px;
      height: 295px;
      .gasCharts {
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
    }
    .storeGas {
      width: 739px;
      height: 480px;
      margin-bottom: 20px;
      .storeContent {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .tabs {
          width: 56px;
          height: 200px;
          margin-top: 35px;
          overflow: scroll;
          overflow-x: hidden;
          &::-webkit-scrollbar {
            width: 0 !important;
          }
          div {
            height: 32px;
            width: 100%;
            color: #fff;
            border-radius: 8px;
            line-height: 32px;
            text-align: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            margin-bottom: 20px;
            box-sizing: border-box;
            padding: 0 10px;
          }
          .active {
            background: #3a7ff2;
          }
        }
      }
      .storeData {
        display: flex;
        margin-top: 25px;
        box-sizing: border-box;
        padding-left: 40px;
        div {
          width: 217px;
          height: 40px;
          background: url("../assets/rhomboidBorder.png");
          background-size: 100%;
          background-position: 100% 100%;
          background-repeat: no-repeat;
          box-sizing: border-box;
          padding: 0 40px;
          line-height: 40px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          font-size: 16px;
          & span:last-child {
            color: #f8b62d;
          }
        }
      }
      & .storeData:last-child {
        justify-content: flex-end;
      }
    }
  }
}

.box {
  position: relative;
}
</style>